import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import './style.css';

import PostPreview from './PostPreview';
import PostEditor from './PostEditor';

import { postActions } from './_actions';
import { alertActions } from '../Alert/_actions';
const {
    addPost,
    updatePost,
    clearCurrentPost
    } = postActions;
const { flash } = alertActions;

/**
 * 
 * 
 * 
 *
 * 
 * 
 * 
 */


const Header = ({...props, isPublished}) => (
    <div className='write__nav'>
        <button
            className='write__back'
            onClick={ props.handleBackClick }
            ></button>
        <button
            className='write__publish'
            onClick={ props.handleClick }
            >
            {
                isPublished
                    ? ''
                    : ''
            }
        </button>
    </div>
);


//  store   state 
//
class Write extends React.Component {
    constructor(props) {
        super(props);
        const { isPublished } = this.props;
        if(isPublished) {                                   // 
            const { id, title, body } = this.props.detail;
            this.state = {
                id,
                title,
                body,
                category: ''
            }
        } else {
            this.state = {                                      // 
                title: '',
                body: '',
                category: ''
            }
        }
    }


    handleClick() {                                         // 
        const { dispatch } = this.props;
        const {
            title,
            body
        } = this.state;

        const { isPublished, postId} = this.props;          //  ID  store 
                                                            // 

        if(title.trim() && body.trim()) {                   // 

            // 
            dispatch(
                isPublished
                    ? updatePost(postId, title, body)
                    : addPost(title, body)
            );
        } else {
            dispatch(flash({ message: ',' }));
        }

    }

    handleBackClick() {
        this.clearCurrentPostData();
        this.props.history.goBack()
    }
    clearCurrentPostData() {
        const { dispatch } = this.props;
        dispatch(clearCurrentPost())
        console.log('')
    }

    handleTitleChange(e) {
        this.setState({
            title: e.target.value
        })
    }

    handleBodyChange(e) {
        this.setState({
            body: e.target.value
        })
    }

    render() {
        return (
            <div className='main'>
                <div className='write'>
                    <Header
                        isPublished={ this.props.isPublished }
                        handleClick={ this.handleClick.bind(this) }
                        handleBackClick={ this.handleBackClick.bind(this) }
                     />
                    <PostEditor
                        title={ this.state.title }
                        body={ this.state.body }
                        handleTitleChange={ this.handleTitleChange.bind(this) }
                        handleBodyChange={ this.handleBodyChange.bind(this) }
                    />
                    <PostPreview
                        title={ this.state.title }
                        body={ this.state.body }
                    />
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const { post } = state;
    return {
        postId: post.postId,
        isPublished: post.isPublished,
        detail: post.detail
    }
}

const WriteWithRouter = withRouter(Write);


export default connect(mapStateToProps)(WriteWithRouter);



