import React from 'react';
import { Redirect, Route } from 'react-router-dom';



// 
//  token 
export const PrivateRoute = ({ component: Component, ...rest }) => (
    <Route
        { ...rest }
        render={
                props => (
                    localStorage.getItem('token')
                        // false                                        // 
                        ? <Component { ...props } />
                        : <Redirect to={{ pathname: '/login'}} />
                    )
                }
    />
);






