import { authActionTypes } from './_actionType';
import { api } from '../../api';


/**
 * 
 * 
 * 
 *
 * 
 *
 *  RFC  200 
 *
 */
import { alertActions } from '../Alert/_actions';
const { flash } = alertActions;

export const authActions = {
    login,
    logout,
    register,
    updateCurrentUserId
};

function login(email, password) {
    return dispatch => {
        dispatch(request());
        api.post('/api/login', { email, password })
            .then(res => {
                if(res.status === 200) {
                    const { token, refreToken } = storeTokens(res);               //  tokens

                    const { message, userId } = res.data;
                    localStorage.setItem('userId', JSON.stringify(userId));                     //  ID 

                    //  ID 
                    dispatch(success(
                            {
                                message,
                                token,
                                refreToken,
                                userId
                            }
                        )
                    );

                    dispatch(flash(
                            {
                                alertType: 'good',
                                message
                            }
                        )
                    );


                    //  tokens
                    // setTimeout(() => {
                        // localStorage.clear();
                        // dispatch(flash({ alertType: 'bad', message: ''}))
                        // console.log('xxx')
                    // }, 4000)
                }
            })
            .catch(err => {
                const { response } = err;
                if(response.status === 404) {
                    const { message } = response.data;

                    dispatch(failure(
                            {
                                message
                            }
                        )
                    );

                    dispatch(flash(
                            {
                                alertType: 'bad',
                                message
                            }
                        )
                    );
                }
            }
        );
    }

    function request() {
        return {
            type: authActionTypes.LOGIN_REQUEST,
        }
    }

    function success(store) {
        return {
            type: authActionTypes.LOGIN_SUCCESS,
            message: store.message,
            userId: store.userId,
            token: store.token,
            refreToken: store.refreToken,
        }
    }

    function failure(store) {
        return {
            type: authActionTypes.LOGIN_FAILURE,
            message: store.message
        }
    }

}



// 
function logout() {

}

function register(newUser) {
    return dispatch => {
        dispatch(request());

        api.post('/api/register', newUser)
            .then(res => {
                if(res.status === 200) {                                        // 
                    const { message } = res.data;

                    dispatch(success(
                            {
                                message,
                            }
                        )
                    );

                    dispatch(flash(
                            {
                                alertType: 'good',
                                message
                            }
                        )
                    );

                    dispatch(login(
                            newUser.email,
                            newUser.password
                        )
                    );           // 
                }
            })
            .catch(err => {
                const { response } = err;
                if(response.status === 400) {
                    const { message, errorType } = response.data;

                    dispatch(failure(
                            {
                                message,
                                errorType
                            }
                        )
                    );
                    dispatch(flash(
                            {
                                alertType: 'bad',
                                message
                            }
                        )
                    );
                }
            }
        );
    }

    function request() {
        return {
            type: authActionTypes.REGISTER_REQUEST
        }
    }

    function success(store) {
        return {
            type: authActionTypes.REGISTER_SUCCESS,
            message: store.message,
        }
    }

    function failure(store) {
        return {
            type: authActionTypes.REGISTER_FAILURE,
            message: store.message,
            errorType: store.errorType
        }
    }
}

function storeTokens(res) {
    //  tokens 
    const token = res.headers['x-token'];
    const refreToken = res.headers['x-refresh-token'];
    localStorage.setItem('token', JSON.stringify(token));
    localStorage.setItem('refreshToken', JSON.stringify(refreToken));
    return {
        token,
        refreToken
    }
}

function updateCurrentUserId() {
    return {
        type: authActionTypes.UPDATECURRENTUSERID
    }
}



