import React from 'react';
import { CSSTransitionGroup } from 'react-transition-group' // ES6
import './style.css';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';

import { Header } from './Header';

import { authActions } from './_actions';
const { register } = authActions;

const Presentation = ({ ...props }) => (
    <div className='auth__register'>
        <Header />

        <CSSTransitionGroup
            transitionName="register__animate"
            transitionAppearTimeout={500}
            transitionAppear={true}
            transitionEnter={false}
            transitionLeave={false}>

                <form className='register' onSubmit={ props.handleSubmit }>
                    <div className='register__username'>
                        <label htmlFor="username"></label>
                        <input
                            onChange={ props.handleUsernameChange }
                            type="text"
                            name="username"
                            placeholder=""/>
                    </div>
                    <div className='register__email'>
                        <label htmlFor="email"></label>
                        <input
                            onChange={ props.handleEmailChange }
                            type="text"
                            name="email"
                            placeholder=""/>
                    </div>
                    <div className='register__password'>
                        <label htmlFor="passwordForSure"></label>
                        <input
                            onChange={ props.handlePasswordForSureChange }
                            type="password"
                            name="passwordForSure"
                            placeholder=""/>
                    </div>
                    <div className='register__password_confirm'>
                        <label htmlFor="password"></label>
                        <input
                            onChange={ props.handlePasswordChange }
                            type="password"
                            name="password"
                            placeholder=""/>
                    </div>
                    <div className='register__submit'>
                        <input
                            disabled={ props.isLoggingIn }
                            type="submit"
                            value={ props.isRegistering ? '' : ''}/>
                    </div>
                </form>

        </CSSTransitionGroup>
    </div>
);

class Register extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            email: '',
            password: '',
            passwordForSure: ''
        }
    }

    handleSubmit(e) {
        e.preventDefault();

        const { username, email, password } = this.state;
        const newUser = {
            username,
            email,
            password
        }
        // this.validatePassword();         // 
        this.register(newUser);
    }

    register(newUser) {
        const { dispatch } = this.props;
        // dispatch(register(newUser));                   // 
        // dispatch(register({ username: 'ssskdfj', email: 'xiaoshitou@gmail.com', password: 'xiaoshitou' }));                   // 
        dispatch(register(newUser));
    }

    handleUsernameChange(e) {
        this.setState({
            username: e.target.value
        })

    }

    // 
    // 
    handleEmailChange(e) {
        this.setState({
            email: e.target.value
        })
    }

    handlePasswordChange(e) {
        this.setState({
            password: e.target.value
        })
    }

    handlePasswordForSureChange(e) {
        this.setState({
            passwordForSure: e.target.value
        })
    }


    // validatePassword() {
        // // 
        // // 
        // const { password, passwordForSure } = this.state;
        // if(password !== passwordForSure) {
            // console.log('');
        // }

    // }

    render() {
        // const { isRegistering, token, userId } = this.props;
        const { isRegistering, token } = this.props;
        return (
            token
                ? <Redirect to='/' />
                : <div className='auth'>
                    <Presentation
                        isRegistering={ isRegistering }
                        handleSubmit={ this.handleSubmit.bind(this) }
                        handleUsernameChange={ this.handleUsernameChange.bind(this) }
                        handleEmailChange={ this.handleEmailChange.bind(this) }
                        handlePasswordChange={ this.handlePasswordChange.bind(this) }
                        handlePasswordForSureChange={ this.handlePasswordForSureChange.bind(this) }
                    />
                </div>
        );
    }

}

//  token 
//  token 
//  state 
const mapStateToProps = (state) => {
    const { isRegistering, token, userId } = state.auth;
    return {
        isRegistering,
        token,
        userId
    }
}

export default connect(mapStateToProps)(Register);



