class Profile extends React.Component {
    componentDidMount() {
        // const userId  = localStorage.getItem('userId');             // id  URL !!!!!!!
        const { match } = this.props;
        const userId = match.params.id;
        this.fetchUser(userId);
    }


    fetchUser(id) {
        const { dispatch } = this.props;
        dispatch(fetchUser(id))
    }

    isCurrentUser() {
        const { targetUserId, currentUserId } = this.props;
        return parseInt(currentUserId, 10) === targetUserId;
    }

    handleFollowClick() {
        const { isFollowing } = this.props;
        const { dispatch } = this.props;

        const userId  = localStorage.getItem('userId');
        const { targetUserId } = this.props;
        console.log(targetUserId)
        if(isFollowing) {
            dispatch(unfollow(userId, targetUserId, true));
        } else {
            dispatch(follow(userId, targetUserId, true));           // true  fromProfile
            //  bug 
            // 
            // 
        }
    }

    handleLogoutClick() {
        localStorage.clear();
        const { dispatch } = this.props;
        dispatch(flash({ message: '......' }));
        setTimeout(() => {
            window.location.reload();
        }, 1000);               // 1
    }


    render() {
        const props = this.props;
        const isCurrentUser = this.isCurrentUser();
        // console.log(isCurrentUser)
        return (
            <Presentation
                handleLogoutClick={ this.handleLogoutClick.bind(this) }
                handleFollowClick={ this.handleFollowClick.bind(this) }
                isFollowing={ props.isFollowing }
                isCurrentUser={ isCurrentUser }
                id={ props.id }
                username={ props.username }
                email={ props.email }
                avatarHash={ props.avatarHash }
                aboutMe={ props.aboutMe }
                followerCount={ props.followerCount }
                followingCount={ props.followingCount }
            />
        );
    }
}

const mapStateToProps = (state) => {
    const { currentUserId } = state.auth;
    const {
        id,
        username,
        email,
        avatarHash,
        aboutMe,
        followerCount,
        followingCount,
        isFollowing
    } = state.user;


    return {
        currentUserId,
        targetUserId: id,
        username,
        email,
        avatarHash,
        aboutMe,
        followerCount,
        followingCount,
        isFollowing
    }
}


export default connect(mapStateToProps)(Profile);



