import React from 'react';
import ReactMarkdown from 'react-markdown';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { postActions } from './_actions';
import { authActions } from '../Auth/_actions';
const {
    fetchPost,
    clearCurrentPost,
    deletePost,
    } = postActions;
const { updateCurrentUserId } = authActions;


// 
// 
// 
const Header = ({...props, isCurrentUser}) => (
    <div className='post__nav'>
    <button
        className='post__back'
        onClick={ props.handleBackClick }
        ></button>
        {
            isCurrentUser
                        ?
                            <div>
                                <Link
                                    className='post__edit'
                                    to='/write'
                                    onClick={ props.handleEditorClick }
                                    ></Link>
                                <button
                                    className='post__delete'
                                    onClick={ props.handleDeleteClick }
                                    ></button>
                            </div>
                        : ''

        }
    </div>
);


class PostDetail extends React.Component {
    componentDidMount() {
        const { dispatch, match } = this.props;
        const postId = match.params.id;
        dispatch(updateCurrentUserId());            // 
        dispatch(fetchPost(postId));
    }

    // 
    handleBackClick() {
        this.props.history.goBack();
        this.clearCurrentPostData();
    }
    clearCurrentPostData() {
        const { dispatch } = this.props;
        dispatch(clearCurrentPost())
    }

    // 
    handleEditorClick() {
    }

    // (
    // 
    handleDeleteClick() {
        const { dispatch, postId } = this.props;
        this.props.history.goBack();
        dispatch(deletePost(postId));
    }

    isCurrentUser() {
        const { id, currentUserId } = this.props;
        return parseInt(currentUserId, 10) === id;
    }

    render() {
        const { title, body, createdAt } = this.props;          // 
        const isCurrentUser = this.isCurrentUser();
        return (
            <div className='main'>
                <Header
                    isCurrentUser={ isCurrentUser }
                    handleBackClick={ this.handleBackClick.bind(this) }
                    handleEditorClick={ this.handleEditorClick.bind(this) }
                    handleDeleteClick={ this.handleDeleteClick.bind(this) }
                />
                <div className='post'>
                    <h1 className='post__title'>{ title }</h1>
                    <div className='post_infor'>
                        <p><span>{ createdAt }</span></p>
                    </div>
                    <ReactMarkdown className='markdown' source={ body } />
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const { detail } = state.post;
    const { currentUserId } = state.auth;
    return {
        id: detail['author_id'],
        currentUserId,
        postId: detail.id,
        title: detail.title,
        body: detail.body,
        createdAt: detail.createdAt,
        updatedAt: detail.updatedAt,
    }
};

export default connect(mapStateToProps)(PostDetail);



